<template>
  <div>
    <comp-header-login/>
    <!--================Home Banner Area =================-->
    <section class="banner_area">
      <div class="banner_inner d-flex align-items-center">
        <div class="container">
          <div class="banner_content text-center">
            <h2>List Knowledge</h2>
            <div class="page_link">
              <router-link to="/home">Home</router-link>
              <router-link to="/knowledge">List Knowledge</router-link>
            </div>
          </div>
        </div>
      </div>
    </section>
    <!--================End Home Banner Area =================-->

    <!--================Content Area =================-->
    <section class="cat_product_area section_gap">
      <div class="container-fluid">
        <div class="row flex-row-reverse">
          <div class="col-lg-10 py-5">
            <div class="col-lg-11 mx-auto">
              <div class="card rounded shadow border-0">
                <div class="tableTl">Knowledge Table</div>
                <div class="card-body bg-white rounded">
                  <div class="table-responsive">
                    <div style="padding: 20px;">
                      <div class="justify-content-centermy-1 row">
                        <b-form-group horizontal label="Rows per page:" class="col-2">
                          <b-form-select size="sm"
                                         :options="[{text:5,value:5},{text:10,value:10},{text:15,value:15},{text:20,value:20}]"
                                         v-model="perPage">
                          </b-form-select>
                        </b-form-group>

                        <b-form-group label="Search:" class="col-4 searchTab">
                          <b-input-group size="sm">
                            <b-form-input v-model="filter" type="search" placeholder="Type to Search"></b-form-input>
                            <b-input-group-append>
                              <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
                            </b-input-group-append>
                          </b-input-group>
                        </b-form-group>

                      </div>
                      <!-- Main table element -->
                      <b-table striped hover :items="items" :current-page="currentPage" stacked="md"
                               show-empty
                               small
                               :per-page="perPage" :filter="filter" :fields="fields" id="my-table"
                               @filtered="onFiltered">
                      </b-table>
                      <div style="padding-top: 20px">
                        <b-pagination size="md" :total-rows="totalRows" :per-page="perPage"
                                      v-model="currentPage" aria-controls="my-table"/>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-lg-2 py-5">
            <comp-left-sider/>
          </div>
        </div>
      </div>
    </section>

    <comp-back-to-top/>
    <comp-footer/>
  </div>
</template>

<script>
import CompHeaderLogin from "../frame/CompHeaderLogin";
import CompFooter from "../frame/CompFooter";
import CompBackToTop from "../frame/CompBackToTop";
import CompLeftSider from "../frame/CompLeftSider";

export default {
  name: 'CompListKnowledge',
  components: {
    CompHeaderLogin, CompFooter, CompBackToTop, CompLeftSider
  },
  data() {
    return {
      items: [],
      currentPage: 1,
      perPage: 5,
      filter: "",
      totalRows: 1,
      fields: [
        {
          key: 'knowledgeName',
          label: 'Name',
          sortable: true
        },
        {
          key: 'knowledgeDate',
          label: 'Date',
          sortable: true
        },
        {
          key: 'Username',
          label: 'Username',
          sortable: true,
        }
      ],
    }
  },
  methods: {
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  },
  created() {
    const axios = require('axios');
    axios
      .get('http://localhost:1323/knowledge')
      .then(response => {
        this.items = response.data
        this.totalRows = response.data.length
      })
      .catch(error => {
        console.log(error)
      })
  }
}
</script>

<style scoped>
.tableTl {
  text-align: center;
  font-weight: bold;
  font-size: 23px;
  margin-top: 30px;
  color: #2c3e50;
}

.searchTab {
  margin-left: 390px;
}
</style>



